.subscription {
  display: flex;
  justify-content: space-between;
  max-width: 1024px;
  margin: 0 auto;
  padding: 24px;

  background-color: #ffffff;
  border-radius: 5px;

  box-sizing: border-box;
}

.subscription h3 {
  width: 275px;
  margin: 0;
  margin-bottom: 16px;

  font-weight: 600;
  font-size: 24px;
  line-height: 23px;
  letter-spacing: -0.01em;
  color: #222222;
}

.subscription p {
  margin: 0;
  margin-bottom: 16px;

  font-weight: 300;
  font-size: 16px;
  line-height: 19px;
  color: #7D7D7D;
}

.subscription__form {
  display: flex;
  margin-bottom: 16px;
}

.subscription__form input {
  width: 413px;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 16px;

  border: 1px solid #E0E0E0;
  border-radius: 5px 0 0 5px;

  box-sizing: border-box;

  &::placeholder {
    color: #7D7D7D;
  }
}

.subscription__button {
  width: 213px;
  padding-top: 15px;
  padding-bottom: 15px;

  color: #00AED6;

  border: 1px solid #E0E0E0;
  border-left: none;
  border-radius: 0 5px 5px 0;
  background-color: transparent;

  box-sizing: border-box;
  cursor: pointer;
}

.subscription__social {
  margin-bottom: 24px;
}

.subscription__list,
.subscription__items {
  display: flex;

  border: 1px solid #E0E0E0;
  border-radius: 5px;
}

.subscription__icon {
  width: 56px;
  height: 48px;
  border-right: 1px solid #E0E0E0;
}

.subscription__fb {
  margin-top: 12px;
  margin-left: 23px;
  fill: #3C5A99;
}

.subscription__vk {
  margin-top: 15px;
  margin-left: 13px;
  fill: #5181B8;
}

.subscription__od {
  margin-top: 11px;
  margin-left: 20px;
  fill: #F7931E;
}

.subscription__youtube {
  margin-top: 14px;
  margin-left: 15px;
}

.subscription__icon--inst {
  background-image: url("../img/icon-insta.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 27px 27px;
}

.subscription__icon--youtube {
  border-right: none;
  stroke: #E0E0E0;
  fill: #FF0000;
}

// .icon-youtube-play {
//   fill: #ffffff;
// }

.subscription__app {
  height: 48px;
}

.subscription__app--apple {
  width: 136px;

  background-image: url("../img/app-store.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 92px 23px;
  border-right: 1px solid #E0E0E0;
}

.subscription__app--android{
  width: 142px;

  background-image: url("../img/google-play.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100px 24px;
}
